import React from 'react';
import { navigate } from 'gatsby';
import MuiLink from '@mui/material/Link';

const Link = props => {
  const children = props.children || props.href;

  // target="_blank"  Ex/In  a  (MuiLink) 
  if (props.target === '_blank') {
    return (
      <MuiLink color='inherit' {...props} rel='noopener noreferrer'>
        {children}
      </MuiLink>
    );
  }

  //  MuiLink
  if (/^(http:|https:)?\/\/.+$/.test(props.href)) {
    return (
      <MuiLink color='inherit' {...props}>
        {children}
      </MuiLink>
    );
  }

  //  MuiLink + navigate 
  return (
    <MuiLink
      color='inherit'
      {...props}
      onClick={e => {
        e.preventDefault();
        navigate(props.href);
      }}
    >
      {children}
    </MuiLink>
  );
};

export default Link;
